.notification {
    font-family: "Open Sans", sans-serif;
    font-weight: 800;

    color: white;

    border: 2px solid black;
    border-radius: 8px;

    width: 200px;
    
    padding: 30px;

    margin: 1rem;

    text-align: center;
    vertical-align: middle;

    transition: 1s all;
}